}

.card_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card_row_big {
  height: 64px;
}

.card_list {
  position: relative;
  display: flex;
  max-width: 435px;
}

.link {
  text-decoration: none;
}